import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Home from './home';

export class DisplayNewsInfo extends Component{
    render(){
        
        return(<div id='displayNewsInfo'>
                <h1 style={{margin:'50px',borderBottom:'4px solid #ccc', width:'50%'}}>{this.props.title}</h1>
                <img src={this.props.imgPath} alt="خبر" 
                                 style={{marginRight:'auto',marginLeft:'auto'}}
                />
                
                <h3 style={{margin:'50px',borderBottom:'0px solid #ccc', width:'70%', textAlign:'justify'}}>{this.props.paragraph}</h3>
                <button
                    style={{fontSize:'20px', width:'100px', textAlign:'justify'}}
                    onClick ={()=>ReactDOM.render(<Home />,document.querySelector("#bodyContainer"))}
                >عودة</button>
                </div>
        );
    }
}

