import React , {Component} from "react";
import ReactDOM from 'react-dom';
import Home from './home'

export class DisplayNewsInfo extends Component{
    render(){
        
        return(<div id='displayNewsInfo'>
                <h1 style={{margin:'50px',borderBottom:'4px solid #ccc', width:'50%'}}>{this.props.title}</h1>
                <img src={this.props.imgPath} alt="خبر" 
                                 style={{marginRight:'auto',marginLeft:'auto'}}
                />
                
                <h3 style={{margin:'50px',borderBottom:'0px solid #ccc', width:'70%', textAlign:'justify'}}>{this.props.paragraph}</h3>
                <button
                    style={{fontSize:'20px', width:'100px', textAlign:'justify'}}
                    onClick ={()=>ReactDOM.render(<Home />,document.querySelector("#bodyContainer"))}
                >عودة</button>
                </div>
        );
    }
}

export default class RacingNews extends React.Component {
  render() {
    return (
      <>
      {/* <img id='bbg'/>Body Back Ground */}
      {/* <p>اخبار الميدان  {this.props.title}</p> */}
      {this.props.allRacingNews.map((news)=>{
        //   return<div id='raceTitleBox'>{news}</div>
        return <div id='allNewsLinksContainer'><p 
        onClick ={()=>ReactDOM.render(<DisplayNewsInfo title={news.title} imgPath={news.imgPath} paragraph={news.paragraph} />,document.querySelector("#bodyContainer"))}
        >{news.title}</p></div>
    })
      }
      </>
    );
    
  }
}
